main .Section1 form input.error::-ms-input-placeholder{color:#f46262}main .Section1 form input.error::placeholder{color:#f46262}main .Section1 form span{font-size:11px;font-style:italic;color:#f46262}main .Section1 form button{color:white;font-size:17px;font-weight:700}main div.shortLink .url{font-size:17px}main div.shortLink .shortenUrl{color:#2acfcf;font-size:15px}main div.shortLink .shortenUrl button{color:white;font-weight:700;font-size:15px}main .Section2 p{color:#9e9aa7;font-size:15px;line-height:1.7}main .Section2 .advancedStatistics{text-align:center}main .Section2 .advancedStatistics h1{font-size:25px;font-weight:700;margin:0 0 10px 0}main .Section2 .IconComponents h1{font-size:20px;font-weight:700;margin:0 0 10px 0}footer .Section1 span{display:block;margin:0 auto 18px auto;font-size:29px;font-weight:700;color:white}footer .Section1 button{background-color:#2acfcf;color:white;font-weight:700;font-size:18px}footer .FooterSection2 .Shortly{font-weight:700;font-size:30px}footer .FooterSection2 ul .title{font-weight:700;font-size:15px}footer .FooterSection2 ul li{font-size:14px;font-weight:200;color:#bfbfbf}

