.NavBarDesktop{display:none}@media screen and (min-width: 963px){.NavBarDesktop{display:flex;width:75%;max-width:80%;justify-content:space-between;align-items:center}.NavBarDesktop ul:nth-of-type(1){width:250px;display:flex;justify-content:space-between}.NavBarDesktop ul:nth-of-type(2){width:150px;display:flex;justify-content:space-between}.NavBarDesktop ul:nth-of-type(2) li{align-self:center}.NavBarDesktop ul:nth-of-type(2) li button{border-radius:20px;background-color:#2acfcf;border:none;width:100px;height:40px}.NavBarDesktop ul:nth-of-type(2) li button:hover{background-color:#aee}}

main{background-color:#e6e6e6;padding:121px 22px 90px 22px;position:relative}

