div.shortLink{background-color:white;border-radius:10px;margin:0 auto 20px auto;max-width:500px}@media screen and (min-width: 1000px){div.shortLink{max-width:851px;padding:0px 21px;display:flex;justify-content:space-between;flex-wrap:wrap}}div.shortLink .url{border-bottom:1px solid #e6e6e6;padding:17px}@media screen and (min-width: 1000px){div.shortLink .url{border:none;padding:0;align-self:center}}div.shortLink .shortenUrl{display:flex;flex-direction:column;padding:17px}@media screen and (min-width: 1000px){div.shortLink .shortenUrl{flex-direction:row;justify-content:space-between;width:35%;align-items:center}}div.shortLink .shortenUrl span{margin:0 0 15px 0}@media screen and (min-width: 1000px){div.shortLink .shortenUrl span{width:20%;margin:0}}div.shortLink .shortenUrl button{background-color:#2acfcf;border:none;padding:15px 10px;border-radius:10px}div.shortLink .shortenUrl button:hover{background-color:#aee}@media screen and (min-width: 1000px){div.shortLink .shortenUrl button{width:35%;padding:10px}}div.shortLink .shortenUrl button.Copied{background-color:#35323e}

.Section2{padding:60px 0 0 0;position:relative;max-width:1440px;margin:0 auto}@media screen and (min-width: 1000px){.Section2{display:flex;flex-wrap:wrap;padding-bottom:150px}}.Section2 .greenTrace{width:9px;height:900px;top:50%;left:50%;-webkit-transform:translate(-50%, -40%);transform:translate(-50%, -40%);background-color:#2acfcf;position:absolute}@media screen and (min-width: 1000px){.Section2 .greenTrace{width:900px;height:9px;-webkit-transform:translate(-50%, 80px);transform:translate(-50%, 80px)}}.Section2 .advancedStatistics{width:100%}@media screen and (min-width: 1000px){.Section2 .advancedStatistics p{max-width:500px;display:block;margin:0 auto}}

